import { BlogEntry } from "@/models/blog";
import PostSmall from "../post-small";

export default function PostList({ posts, isLoading }: { posts: BlogEntry[], isLoading: boolean }) {
    return (
        <>
            {posts.map((post) => (
                <PostSmall key={post._id!.toString()} post={post} />
            ))}
        </>
    )
}